@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
*{padding:0;margin:0;font-family:'Ubuntu'}#home{background-size:cover;background-position:center;background-image:url(/static/media/background.886715e5.jpg);display:flex;justify-content:space-around;height:100vh;width:100vw}#home section{background-color:rgba(255,255,255,0.8);border-radius:5px;padding:0 2rem;height:40%;width:35%}#home .fa-lightbulb{margin-top:3rem;font-size:7rem;color:#ff8c00}#home h1{text-align:center;margin-top:.5rem;font-size:3rem;color:black}#home .take-button-container{margin-top:1.5rem;width:100}#home a.take-button{background-color:#0080ff;border-radius:25px;color:white;text-align:center;cursor:pointer;padding:.5rem;display:block;transition:0.3s linear all}#home a.take-button:hover{background-color:#06c;box-shadow:0 2px 2px 0 rgba(0,0,0,0.1),0 1px 5px 0px rgba(0,0,0,0.2)}@media (max-width: 750px){#home section{padding:0 1.5rem;width:100%;height:100%}}.questions{background-color:whitesmoke;border-left:5px solid lightblue;width:80%;margin:3rem auto 0 auto;padding:1.5rem 2rem}.questions .hints-container{display:flex;justify-content:right;margin-bottom:0.3rem;margin-right:0.3rem}.questions .fa-exclamation{color:#f43a09;cursor:pointer}.questions .fa-exclamation:hover{color:#901414}.questions .num-and-timer{display:flex;justify-content:space-between}.questions h3{height:10vh;font-size:2.5rem;font-weight:500;margin:1.5rem 0;line-height:1.5;text-align:center}.questions .options-container{display:inline-block;width:50%}.questions .option{font-size:1.25rem;background-color:#0080ff;border-radius:25px;color:white;cursor:pointer;margin:2rem auto;padding:1rem;text-align:center;transition:0.3s linear all;width:90%}.questions .option:hover{background-color:#004d99}.button-container{display:flex;justify-content:flex-start;margin:0 auto 1.5rem auto;padding:2rem;width:100%}.button-container button{border:none;color:black;cursor:pointer;margin-right:2rem;padding:.5rem 1.5rem}.button-container button:first-child{background-color:#ffee42;box-shadow:0 2px 2px 0 rgba(0,0,0,0.1),0 1px 5px 0px rgba(0,0,0,0.2);transition:0.2% linear all;border-radius:5px}.button-container button:first-child:hover{background-color:#ffe90f}.button-container button:nth-child(2){background-color:#8bb046;box-shadow:0 2px 2px 0 rgba(0,0,0,0.1),0 1px 5px 0px rgba(0,0,0,0.2);transition:0.2% linear all;border-radius:5px}.button-container button:nth-child(2):hover{background-color:#6e8c37}.button-container button:last-child{background-color:#9890f0;box-shadow:0 2px 2px 0 rgba(0,0,0,0.1),0 1px 5px 0px rgba(0,0,0,0.2);transition:0.2% linear all;border-radius:5px}.button-container button:last-child:hover{background-color:#6e63ea}@media (max-width: 900px){.questions{width:100%}h3{font-size:1.75rem !important}.option{font-size:1rem !important}}.stat-page{background-size:cover;background-position:center;background-image:url(/static/media/background.886715e5.jpg);height:100vh;width:100vw;text-align:center}.stat-page h1{text-align:center;margin-bottom:1rem}.fa-check-circle{margin:3rem 0 .5rem 0;font-size:10rem;color:#3cb300;text-align:center;width:100%}.stats-container{width:40%;background-color:#fbecdb;opacity:0.8;border-radius:5px;margin-bottom:0.5rem;padding:1rem;display:inline-block;text-align:left}.stats-container h2{text-align:center;color:black;margin-bottom:1rem}.stat-btn-container{display:flex;justify-content:center}.stat-btn-container .btn:first-child{background-color:#59c05f;color:white;margin:0rem 10%}.stat-btn-container .btn:first-child:hover{background-color:#3fa745}.stat-btn-container .btn:last-child{background-color:#115cff;color:white;margin:0 10%}.stat-btn-container .btn:last-child:hover{background-color:#0046dd}.no-stats{background-color:rgba(200,200,200,0.8);padding:3rem;border-radius:5px;vertical-align:middle}.no-stats h1{display:flex;justify-content:center;margin-bottom:2rem}@media (max-width: 1000px){.stats-container{width:50%}}@media (max-width: 750px){.stats-container{width:60%}}@media (max-width: 600px){.stats-container{width:80%}}

